import axios from "../../plugins/axios";

export default ({
    state: () => ({
        users: null,
        role: [],
    }),
    mutations: {
        getUsers(state, users) {
            state.users = users
        },
        addUser(state, newUser) {
            state.users.users.push(newUser)
        },
        delUser(state, id) {
            state.users.users = state.users.users.filter(ele => ele.id !== id)
        },
        changeState(state, { uId, type }) {
            state.users.users.find(ele => ele.id === uId).mg_state = type
        },
        searchId(state, id) {
            state.users.users = state.users.users.find(ele => ele.id === id)
        },
        editItem(state, payload) {
            const ind = state.users.users.findIndex((ele) => ele.id === payload.id);
            state.users.users.splice(ind, 1, payload);
        },
        getRole(state, role) {
            state.role = role
        },
        setRole(state, {id,selectedRoleId}) {
            const roleName = state.role.find(
                (el) => el.id === selectedRoleId
            ).roleName;
            state.users.users.find((el) => el.id === id).role_name = roleName;
        }
    },
    actions: {
        async getUsers({ commit }, payload) {
            const res = payload.query ? await axios.get(`/users?query=${payload.query}&pagenum=${payload.pagenum}&pagesize=${payload.pagesize}`) : await axios.get(`/users?pagenum=${payload.pagenum}&pagesize=${payload.pagesize}`)
            console.log(res.data);
            commit('getUsers', res.data)
        },
        async addUser({ commit }, { username, password, email, mobile }) {
            const res = await axios.post('/users', { username, password, email, mobile })
            console.log(res.data);
            commit('addUser', res.data)
        },
        async delUser({ commit }, id) {
            await axios.delete(`/users/${id}`)
            commit('delUser', id)
        },
        async changeState({ commit }, { uId, type }) {
            const res = await axios.put(`/users/${uId}/state/${type}`)
            console.log(uId);
            console.log(type);
            console.log(res);
            commit('changeState', { uId, type })
        },
        async searchId({ commit }, id) {
            await axios.get(`/users/${id}`)
            commit('searchId', id)
        },
        async editItem({ commit }, payload) {
            console.log(payload);
            const newUser = {
                id: payload.id,
                email: payload.email,
                mobile: payload.mobile,
            }
            const res = await axios.put(`users/${payload.id}`, newUser)
            console.log(res);
            commit('editItem', payload)
        },
        // async roleItem({ commit }, { id, rid }) {
        //     await axios.put(`users/${id}/role`, { id: id, rid: rid })
        // },
        async currentChange({ dispatch }, { value, pagesize, query }) {
            const res = query ? await axios.get(`/users?query=${query}&pagenum=${value}&pagesize=${pagesize}`) : await axios.get(`/users?pagenum=${value}&pagesize=${pagesize}`)
            console.log(res.data);
            dispatch('getUsers', res.data)
        },
        // 获取角色
        async getRole({ commit }) {
            const res = await axios.get('/roles')
            console.log(res.data);
            commit('getRole', res.data)
        },
        // 设置角色
        async setRole({ commit }, { id, selectedRoleId }) {
            console.log(id, selectedRoleId);
            const res = await axios.put(`/users/${id}/role`, { rid: selectedRoleId })
            console.log(res.data);
            commit('setRole', {id,selectedRoleId})
        }
    }
})
