<template>
  <div class="auth">
      <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>权限列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table v-if="list" :data="list"  border style="width: 100%">
      <el-table-column label="#" width="80"> </el-table-column>
      <el-table-column prop="authName" label="权限名称" width="350">
      </el-table-column>
      <el-table-column prop="path" label="路径" width="350">
      </el-table-column>
      <el-table-column prop="level" label="权限等级" width="350">
          <template v-slot='{row}'>
              <el-tag size='small' :type="row.level === '0' ? '' : row.level === '1' ? 'success' : 'warning' ">
              {{row.level === '0' ? '一级' : row.level === '1' ? '二级' : '三级' }}
              </el-tag>
          </template>
      </el-table-column>
      
    </el-table>
    </el-card>
  </div>
</template>

<script>
import {mapActions,mapState} from 'vuex'
export default {
    computed: {
        ...mapState({
            list: state => state.auth.list
        })
    },
    methods: {
       ...mapActions(['getAuthList'])
    },
    created () {
        this.getAuthList('list')
    },
}
</script>

