import axios from "../../plugins/axios";

export default ({
    state: () => ({
        list:[]
    }),
    mutations:{
        getAuthList(state,list){
            state.list = list
        }
    },
    actions:{
        // 获取权限列表
       async getAuthList({commit},type){
            const res = await axios.get(`rights/${type}`)
            console.log(res.data);
            commit('getAuthList',res.data)
        }
    }
})