<template>
  <div class="login">
    <div class="login-inner">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        class="demo-ruleForm"
        hide-required-asterisk
      >
        <el-form-item prop="username">
          <el-input
            
            v-model="ruleForm.username"
            prefix-icon="el-icon-user-solid"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="ruleForm.password"
            prefix-icon="el-icon-lock"
          ></el-input>
        </el-form-item>
        <el-form-item class="loginBox">
        <el-button type='primary' @click="login">登录</el-button>
        <el-button type='info' @click="resign">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import {mapActions,mapState} from 'vuex'
export default {
  data() {
    return {
      ruleForm: {
        username: "admin",
        password: "123456",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
    };
  },
  computed: {
    ...mapState({
      info: (state) => state.login.info
    })
  },
  methods: {
    ...mapActions(['getInfo']),
    login(){
      this.$refs.ruleForm.validate((boolean) => {
        if (boolean) {
          const {username,password} = this.ruleForm
          this.getInfo({username,password})
            .then(() => {
              this.$router.push("/");
            })
            .catch(() => {
              this.$message.error("错了哦，token有误");
            });
        }
      });
    },
    resign(){
      this.ruleForm = {username:'',password:''}
    }
  },
};
</script>

<style lang='less'>
.login {
    background-color: #2b4b6b;
    min-height: 100vh;
    padding: 200px;
  .login-inner {
    width: 450px;
    padding: 80px 40px 40px;
    margin: 0 auto;
    background-color: #fff;
    
    .el-button{
      text-align: right;
    }
    .el-form-item__label{
      color: #000;
    }
    .loginBox{
      display: flex;
      justify-content: right;
    }
  }
}
</style>