<template>
  <div v-if="users" class="user">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-input
        v-model="query.query"
        placeholder="请输入内容"
        style="width: 300px"
      ></el-input>
      <el-button @click="searchUser" icon="el-icon-search"></el-button>
      <el-button type="primary" @click="add">添加用户</el-button>
      <el-dialog
        :title="form.id ? '编辑用户' : '添加用户'"
        :visible.sync="dialogFormVisible"
      >
        <el-form :model="form" :rules="rules" ref="ruleForm">
          <el-form-item label="用户名" prop="username">
            <el-input
              v-model="form.username"
              :disabled="form.id ? true : false"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item v-if="!form.id" label="密码" prop="password">
            <el-input
              type="password"
              v-model="form.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email">
            <el-input v-model="form.email" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话" prop="mobile">
            <el-input v-model="form.mobile" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </div>
      </el-dialog>
      <el-table :data="users.users" border style="width: 100%">
        <el-table-column label="#" width="60"> </el-table-column>
        <el-table-column prop="username" label="姓名" width="150">
        </el-table-column>
        <el-table-column prop="email" label="邮箱" width="180">
        </el-table-column>
        <el-table-column prop="mobile" label="电话" width="150">
        </el-table-column>
        <el-table-column prop="role_name" label="角色" width="150">
        </el-table-column>
        <el-table-column prop="mg_state" label="状态" width="100">
          <template v-slot="{ row }">
            <el-switch
              v-model="row.mg_state"
              @change="change(row.id, row.mg_state)"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template v-slot="{ row }"
            ><el-button
              @click="edit(row)"
              size="small"
              type="primary"
              icon="el-icon-edit"
            ></el-button
            ><el-button
              @click="del(row.id)"
              size="small"
              type="danger"
              icon="el-icon-delete-solid"
            ></el-button
            ><el-button
              @click="openRole(row)"
              size="small"
              type="success"
              icon="el-icon-s-tools"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="sizeChange"
        @current-change="currentChange"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="2"
        layout="total, sizes, prev, pager, next, jumper"
        :total="users.total"
      >
      </el-pagination>
    </el-card>
    <el-dialog title="分配角色" :visible.sync="dialogVisible">
      <el-form :model="roleForm">
        <el-form-item label="当前的用户：">
          <p>{{ roleForm.username }}</p>
        </el-form-item>
        <el-form-item label="当前的角色：">
          <p>{{ roleForm.role_name }}</p>
        </el-form-item>
        <el-form-item label="分配新角色">
          <el-select v-model="selectedRoleId" placeholder="请选择">
            <el-option
              v-for="item in role"
              :key="item.id"
              :value="item.id"
              :label="item.roleName"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureRole">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
 
<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    let emailRule = /^(\w)+@(\w)+(\.[a-zA-Z]{2,})$/;
    let checkEmail = (rule, value, callback) => {
      if (!emailRule.test(value)) {
        return callback(new Error("邮箱格式不正确"));
      } else {
        callback();
      }
    };
    let mobileRule = /^1[0-9]{10}/;
    let checkMobile = (rule, value, callback) => {
      if (!mobileRule.test(value)) {
        return callback(new Error("手机号不正确"));
      } else {
        callback();
      }
    };
    return {
      query: {
        pagenum: 1,
        pagesize: 2,
        query: "",
      },
      dialogFormVisible: false,
      dialogVisible: false,
      form: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      roleForm: {},
      selectedRoleId: "",
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "长度在 3 到 10 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "长度在 6 到 15 个字符",
            trigger: "blur",
          },
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { validator: checkEmail, trigger: "blur" },
        ],
        mobile: [
          { required: true, message: "请输入手机号", trigger: "blur" },
          { validator: checkMobile, trigger: "blur" },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      users: (state) => state.users.users,
      role: (state) => state.users.role,
    }),
  },
  methods: {
    ...mapActions([
      "getUsers",
      "addUser",
      "delUser",
      "changeState",
      "editItem",
      "getRole",
      "setRole",
    ]),
    currentChange(value) {
      this.query.pagenum = value;
      this.getUsers(this.query);
    },
    // 更改页的展示数量
    sizeChange(value) {
      this.query.pagesize = value;
      this.getUsers(this.query);
    },
    add() {
      this.dialogFormVisible = true;
      this.form = { username: "", password: "", email: "", mobile: "" };
    },
    edit(row) {
      this.dialogFormVisible = true;
      this.form = { ...row };
    },
    sure() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.form.id) {
            this.editItem(this.form).then(() => {
              this.$message.success("修改成功"),
                (this.dialogFormVisible = false),
                (error) => {
                  this.$message.error(error);
                };
            });
          } else {
            this.addUser(this.form).then(() => {
              this.$message.success("添加成功"),
                (this.dialogFormVisible = false),
                (error) => {
                  this.$message.error(error);
                };
            });
          }
        }
      });
    },
    searchUser() {
      this.getUsers(this.query);
    },
    async del(id) {
      await this.delUser(id);
      this.$message({
        message: "删除成功",
        type: "success",
      });
    },
    async change(id, type) {
      await this.changeState({ uId: id, type: type });
      this.$message({
        message: "修改状态成功！",
        type: "success",
      });
    },
    openRole(row) {
      this.dialogVisible = true;
      this.roleForm = { ...row };
      console.log(this.roleForm);
    },
    async sureRole() {
      console.log(this.roleForm);
      const { id } = this.roleForm;
      const { selectedRoleId } = this;
      await this.setRole({ id, selectedRoleId });
      this.dialogVisible = false;
      this.$message.success("更新角色成功");
    },
  },
  created() {
    this.getUsers(this.query);
    this.getRole();
  },
};
</script>

