import axios from '../../plugins/axios'

export default ({
    state: () => ({
        roles:null,
        rights:[]
    }),
    mutations:{
        getRoles(state,roles){
            state.roles = roles
        },
        addRole(state,newRole){
            state.roles.push(newRole)
        },
        editRole(state,payload){
            const ind = state.roles.findIndex((ele) => ele.id === payload.roleId);
            state.roles.splice(ind, 1, payload);
        },
        deleteRole(state,id){
            state.roles = state.roles.filter(ele => ele.id !== id)
        },
        searchId(state,id){
            state.roles.find(ele => ele.id === id)
        },
        getRights(state,rights){
            state.rights = rights
        },
        delRight(state,{roleId,data}){
            state.roles.find(ele => ele.id === roleId).children = data
        }
    },
    actions:{
        async getRoles({commit}){
            const res = await axios.get('/roles')
            console.log(res.data);
            commit('getRoles',res.data)
        },
        async addRole({commit},payload){
            const info = {
                roleName:payload.roleName,
                roleDesc:payload.roleDesc
            }
            const res = await axios.post('/roles',info)
            console.log(res);
            commit('addRole',res.data)
        },
        async editRole({ commit }, payload) {
            console.log(payload);
            const newUser = {
                // id: payload.id,
                roleName: payload.roleName,
                roleDesc: payload.roleDesc,
            }
            const res = await axios.put(`roles/${payload.id}`, newUser)
            console.log(res);
            commit('editRole', res.data)
        },
        async deleteRole({commit},id){
            axios.delete(`/roles/${id}`)
            commit('deleteRole',id)
        },
        async searchId({commit},id){
            const res = axios.get(`/roles/${id}`)
            console.log(res);
            commit('searchId',res.data)
        },
        // 获取所有角色权限
        async getRights({commit},type){
            const res = await axios.get(`/rights/${type}`)
            commit('getRights',res.data)
        },
        // 角色授权
        async authRole({dispatch},payload){
            await axios.post(`roles/${payload.id}/rights`,{rids:payload.rids})
            dispatch('getRoles')
        },
        // 删除权限
        async delRight({commit},{roleId:roleId,rightId:rightId}){
            console.log(roleId);
            console.log(rightId);
            const res = await axios.delete(`roles/${roleId}/rights/${rightId}`)
            console.log(res);
            commit('delRight',{roleId:roleId,data:res.data})
        }
    }
})