import axios from '../../plugins/axios'

export default ({
  state:()=> ({
    goods:null
  }),
  mutations:{
    getGoods(state,goods){
      state.goods = goods
    },
    delGood(state,id){
      state.goods.goods = state.goods.goods.filter(ele => ele.goods_id !== id)
    },
    editGoodItem(state,payload){
      const ind = state.goods.goods.findIndex(ele => ele.goods_id === payload.goods_id)
      state.goods.goods.splice(ind,1,payload)
    }
  },
  actions:{
    async getGoods({commit},payload){
      const res = payload.query ? await axios.get(`goods?query=${payload.query}&pagenum=${payload.pagenum}&pagesize=${payload.pagesize}`) : await axios.get(`/goods?pagenum=${payload.pagenum}&pagesize=${payload.pagesize}`)
      console.log(res.data);
      commit('getGoods',res.data)
    },
    async editGoodItem({commit},payload){
      // delete payload.data.goods_id;
      console.log(payload);
      const res = await axios.put(`goods/${payload.id}`,payload.data)
      console.log(res);
      commit('editGoodItem',res.data)
    },
    // 删除商品
    async delGood({commit},id){
      await axios.delete(`goods/${id}`)
      commit('delGood',id)
    }
  }
})