<template>
  <div class="role">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
    <el-button class="addBtn" @click="add" type="primary">添加角色</el-button>
    <el-dialog
      :title="form.id ? '修改角色' : '添加角色'"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="form.roleName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="form.roleDesc" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="chong">确 定</el-button>
      </div>
    </el-dialog>
    <el-table v-if="roles" :data="roles" border style="width: 100%">
      <el-table-column type="expand" width="80">
        <template v-slot="{ row }">
          <div class="outter">
            <el-row v-for="item in row.children" :key="item.id">
              <el-col :span="5" class="one-outer">
                <el-tag @close='delOneRight(row.id,item.id)' effect="dark" closable>{{ item.authName }}</el-tag
                ><i class="el-icon-caret-right"></i>
              </el-col>
              <el-col :span="19">
                <el-row v-for="item2 in item.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag @close='delOneRight(row.id,item2.id)' effect="dark" closable type="success">{{
                      item2.authName
                    }}</el-tag
                    ><i class="el-icon-caret-right"></i
                  ></el-col>
                  <el-col :span="18">
                    <el-tag
                      class="three"
                      effect="dark"
                      type="warning"
                      closable
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      @close='delOneRight(row.id,item3.id)'
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </div>
        </template>
      </el-table-column>

      <el-table-column label="#" width="80"> </el-table-column>
      <el-table-column prop="roleName" label="角色名称" width="200">
      </el-table-column>
      <el-table-column prop="roleDesc" label="角色描述" width="200">
      </el-table-column>
      <el-table-column label="操作" width="320">
        <template v-slot="{ row }"
          ><el-button
            @click="edit(row)"
            size="small"
            type="primary"
            icon="el-icon-edit"
            >编辑</el-button
          ><el-button
            @click="deleteItem(row.id)"
            size="small"
            type="danger"
            icon="el-icon-delete-solid"
            >删除</el-button
          ><el-button
            @click="openAuth(row)"
            size="small"
            type="success"
            icon="el-icon-s-tools"
            >分配权限</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    </el-card>
    <el-dialog title="分配权限" :visible.sync="dialogVisible" width="60%" @close='closeTree'>
      <el-tree
        :data="rights"
        show-checkbox
        node-key="id"
        default-expand-all
        :props="defaultProps"
        :default-checked-keys="defKeys"
        ref="tree"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sure"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      dialogFormVisible: false,
      dialogVisible: false,
      defKeys:[],
      roleId:'',
      defaultProps: {
        children: "children",
        label: "authName",
      },
      form: {
        roleName: "",
        roleDesc: "",
      },
      rules: {
        roleName: [
          { required: true, message: "请输入角色名称", trigger: "blur" },
        ],
        roleDesc: [
          { required: true, message: "请输入角色描述", trigger: "blur" },
        ],
      },
    };
  },
  created() {
    this.getRoles();
  },
  computed: {
    ...mapState({
      roles: (state) => state.roles.roles,
      rights: (state) => state.roles.rights,
    }),
  },
  methods: {
    ...mapActions([
      "getRoles",
      "addRole",
      "editRole",
      "deleteRole",
      "getRights",
      "authRole",
      "delRight"
    ]),
    add() {
      this.dialogFormVisible = true;
      this.form = { roleName: "", roleDesc: "" };
    },
    edit(row) {
      this.dialogFormVisible = true;
      this.form = { ...row };
    },
    chong() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          if (this.form.id) {
            this.editRole(this.form).then(() => {
              this.$message.success("修改成功"),
                (this.dialogFormVisible = false),
                (error) => {
                  this.$message.error(error);
                };
            });
          } else {
            this.addRole(this.form).then(() => {
              this.$message.success("添加成功"),
                (this.dialogFormVisible = false),
                (error) => {
                  this.$message.error(error);
                };
            });
          }
        }
      });
    },
    async deleteItem(id) {
      await this.deleteRole(id);
      this.$message.success("删除成功");
    },

    // 获取已有权限
    getHadRights(row, arr) {
      if (!row.children) {
        return arr.push(row.id);
      }
      row.children.forEach((ele) => this.getHadRights(ele, arr));
    },
    // 打开分配权限
     openAuth(row) {
      this.getRights("tree");
      this.dialogVisible = true;
      this.getHadRights(row,this.defKeys)
      this.roleId = row.id
    },
    async sure(){
      this.dialogVisible = false
      const keys = [
        ...this.$refs.tree.getCheckedKeys(),
        ...this.$refs.tree.getHalfCheckedKeys(),
      ].join(',')
      await this.authRole({id:this.roleId,rids:keys})
      this.$message.success('分配权限成功')
    },
    // 删除单个 tag
    delOneRight(roleId,rightId){
      this.delRight({roleId,rightId})
    },
    // 关闭 树形控件
    closeTree() {
      this.defKeys = [];
    },
  },
};
</script>

<style lang='less'>
.addBtn {
  display: block;
  margin: 14px 0 20px;
}
.el-row {
  border-bottom: 1px solid #eee;
  display: flex;
  align-items: center;
}
.outter {
  border: 1px solid #eee;
}
.one-outer {
  border-right: 1px solid #eee;
}
.three {
  margin: 6px;
}
</style>