import axios from 'axios'

export default ({
  state: () => ({
    categories: null
  }),
  mutations: {
    getCategories(state, categories) {
      state.categories = categories
    },
    addCatItem(state, res) {
      state.categories.result.find(ele => ele.id === res.pid).children.push(res)
    },
    delCatItem(state, id) {
      state.categories.result = state.categories.result.filter(ele => ele.cat_id !== id)
    },
    editCatItem(state, res) {
      // if(res.cat_level === 0){
      //   const obj = arr.find(ele => ele.cat_id === res.cat_id)
      //   console.log(obj);
      //   obj.cat_name = res.cat_name
      // }else if(res.cat_level === 1){
      //   const parent = arr.find(ele => ele.cat_id === res.cat_pid)
      //   console.log(parent);
      //   const aim = parent.children.find(el => el.cat_id === res.cat_id)
      //   console.log(aim);
      //   aim.cat_name = res.cat_name
      // }else if(res.cat_level === 2){
      //   const one = arr.find(ele => ele.children.cat_id === res.cat_pid)
      //   console.log(one);
      //   const two = one.children.find(el => el.cat_id === res.cat_id)
      //   console.log(two);
      //   two.cat_name = res.cat_name
      // }
      const findObj = (arr, id) => {
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].cat_id === id) {
            const obj = arr[i]
            // console.log(obj);
            return obj
          } else {
            if (arr[i].children) {
              return findObj(arr[i].children, id)
            }
          }
        }
      }
      
      const obj = findObj(state.categories.result,res.cat_id)
      // console.log(obj);
      obj.cat_name = res.cat_name
    }
  },
  actions: {
    async getCategories({ commit }, query) {
      const res = await axios.get(`categories?pagenum=${query.pagenum}&pagesize=${query.pagesize}&type=${query.type}`)
      console.log(res.data);
      commit('getCategories', res.data)
    },
    async addCatItem({ commit }, payload) {
      console.log(payload);
      const res = await axios.post(`categories`, { cat_pid: payload.cat_pid, cat_name: payload.cat_name, cat_level: payload.cat_level })
      console.log(res.data);
      commit('addCatItem', res.data)
    },
    async delCatItem({ commit }, id) {
      await axios.delete(`categories/${id}`)
      commit('delCatItem', id)
    },
    async editCatItem({ commit }, { id, cat_name }) {
      const res = await axios.put(`categories/${id}`, { cat_name: cat_name })
      console.log(res.data);
      commit('editCatItem', res.data)
    }
  }
})