import axios from '../../plugins/axios'

export default ({
  state: () => ({
    orders:null,
    
  }),
  mutations: {
    getOrders(state,res){
      state.orders = res
    },

  },
  actions:{
    async getOrders({commit},query){
      const res = await axios.get(`/orders?pagenum=${query.pagenum}&pagesize=${query.pagesize}&query=${query.query}`)
      console.log(res.data);
      commit('getOrders',res.data)
    },
    // 

    // 查看详情
    // DetailOrder({commit},id){
    //   const res = axios.get(`orders/${id}`)
    //   console.log(res);
    //   commit('DetailOrder',res)
    // },

    // 物流信息
    async getwuliu({commit},id){
      const res = await axios.get(`kuaidi/${id}`)
      console.log(res);
      commit('getwuliu',res.data)
    }
  }
})